@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
.app{box-shadow:2px 2px 5px 2px rgba(0,0,0,0.041),-1px -1px 0px 0px #3e8ecf;border-radius:0.5rem;overflow:hidden;background-color:#ebebeb;width:100%;max-width:58rem;-webkit-flex:1 1;flex:1 1;max-height:40rem;display:grid;grid-template-columns:1fr 3fr 1fr;grid-template-rows:-webkit-max-content 1fr;grid-template-rows:max-content 1fr;grid-template-areas:'header header header' 'conversations chat users'}

.header{grid-area:header;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;background-color:#0f6cb8;padding:0.5rem;border-bottom:2px solid #3e8ecf}.header__left{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header__icon{color:#f5f5f5;font-size:2rem;margin:0 1rem}.header__title{color:#f5f5f5;font-weight:900;font-size:1rem;text-shadow:1px 1px rgba(0,0,0,0.164);text-transform:uppercase;letter-spacing:0.1rem}.header__user{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header__name-btn{color:#f5f5f5;padding:0.5rem;border-radius:0.5rem;background-color:transparent;border:1px solid rgba(255,255,255,0.37);outline:none;font-size:0.9rem;font-weight:700;text-transform:uppercase;margin-right:0.5rem}.header__username-input{color:#f5f5f5;padding:0.5rem;border-radius:0.5rem;background-color:rgba(0,0,0,0.192);border:1px solid rgba(255,255,255,0.37);outline:none;font-size:0.9rem;font-weight:700;text-transform:uppercase;margin-right:0.5rem}

.usericon{border-radius:0.3rem;position:relative;border:2px solid rgba(0,0,0,0.123)}.usericon__svg{width:75%;position:relative;left:50%;top:50%;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%);display:block}.usericon__status{position:absolute;border:2px solid white;width:40%;height:40%;background-color:#2fc900;border-radius:50%;left:70%;top:70%}

.conversations{grid-area:conversations;background-color:#0f6cb8;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:stretch;align-items:stretch;overflow-y:auto}.conversations__channelBtn{cursor:pointer;font-weight:300;font-size:0.9rem;display:block;width:100%;text-align:left;border:none;outline:none;background-color:transparent;color:#f5f5f5;padding:0.5rem;padding-left:1rem}.conversations__channelBtn span{margin-right:0.3rem;font-weight:400;font-size:1rem}.conversations__channelBtn--active{background-color:rgba(0,0,0,0.164)}.conversations__channelBtn:hover{color:#08ccab}.conversations__add-channel{display:-webkit-flex;display:flex}.conversations__add-channel-btn{cursor:pointer;font-weight:300;font-size:0.9rem;background-color:transparent;border:none;outline:none;-webkit-flex:1 1;flex:1 1;margin:1rem;padding:0.5rem;border-radius:0.5rem;color:#f5f5f5;border:1px solid #f5f5f5}.conversations__add-channel-btn:hover{color:#08ccab;border-color:#08ccab}.conversations__add-channel-form{-webkit-flex:1 1;flex:1 1;margin:1rem}.conversations__add-channel-input{font-weight:300;font-size:0.9rem;width:100%;background-color:transparent;border:none;outline:none;padding:0.5rem;border-radius:0.5rem;color:#f5f5f5;border:1px solid #f5f5f5}.conversations__new{background-color:#08ccab;font-weight:900;font-size:0.8rem !important;color:#f5f5f5 !important;border-radius:4px;padding:2px;margin-left:4px;display:-webkit-inline-flex;display:inline-flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}

.chat{grid-area:chat;display:grid;grid-template-rows:-webkit-max-content 1fr -webkit-max-content;grid-template-rows:max-content 1fr max-content;overflow:hidden}.chat__channeltag{padding:0.25rem 1rem;color:#161515;font-weight:400;font-size:1.5rem;border-bottom:2px solid rgba(0,0,0,0.144);box-shadow:1px 1px 2px 2px rgba(0,0,0,0.041)}.chat__channeltag span{margin-right:0.2rem;font-weight:700}.chat__messages{overflow-y:auto;padding:1rem}.chat__content{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.chat__content+.chat__content{margin-top:1rem}.chat__left{margin-right:1rem}.chat__right{position:relative;background-color:#f5f5f5;padding:0.5rem;border-radius:0.35rem;box-shadow:1px 1px 0 0 rgba(0,0,0,0.137);-webkit-flex:1 1;flex:1 1}.chat__pointer{position:absolute;left:0;top:50%;-webkit-transform:translateY(-50%) translateX(-15%) rotate(45deg) skew(-20deg, -20deg);transform:translateY(-50%) translateX(-15%) rotate(45deg) skew(-20deg, -20deg);width:1rem;min-width:1rem;height:1rem;min-height:1rem;background-color:#f5f5f5;z-index:1}.chat__content-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.chat__nametag{font-size:0.7rem;font-weight:700;text-transform:uppercase;color:#161515;z-index:2}.chat__timetag{font-size:0.7rem;font-weight:700;color:#a3a3a3;z-index:2}.chat__texttag{position:relative;font-size:0.9rem;margin-top:0.2rem;word-break:break-all;color:#161515;z-index:100}.chat__form-container{padding:1.5rem 1rem;border-top:2px solid rgba(0,0,0,0.144);position:relative}.chat__form{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:stretch;align-items:stretch}.chat__input{border-radius:0.5rem;-webkit-flex:1 1;flex:1 1;border:none;outline:none;background-color:#f5f5f5;border:1px solid #a3a3a3;font-weight:300;font-size:0.9rem;color:#161515;padding:0.5rem}.chat__button{border-radius:0.5rem;margin-left:0.5rem;padding:0.5rem;background-color:#0f6cb8;border:none;outline:none;color:#f5f5f5;text-transform:uppercase;font-size:0.7rem;font-weight:800;cursor:pointer}.chat__button:hover{background-color:#08ccab}@-webkit-keyframes dot1{0%{-webkit-transform:translateY(-40%);transform:translateY(-40%)}100%{-webkit-transform:translateY(0%);transform:translateY(0%)}}@keyframes dot1{0%{-webkit-transform:translateY(-40%);transform:translateY(-40%)}100%{-webkit-transform:translateY(0%);transform:translateY(0%)}}.chat__typingalert{font-style:italic;font-size:0.8rem;font-weight:400;position:absolute;top:0;left:1rem;z-index:100;padding:0.2rem;border-radius:0.5rem}.chat__dot1{display:inline-block;margin:0rem 0.2rem;-webkit-animation-name:dot1;animation-name:dot1;-webkit-animation-duration:0.4s;animation-duration:0.4s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.chat__dot2{display:inline-block;margin:0rem 0.2rem;-webkit-animation-name:dot1;animation-name:dot1;-webkit-animation-duration:0.4s;animation-duration:0.4s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-delay:-0.2s;animation-delay:-0.2s}.chat__dot3{display:inline-block;margin:0rem 0.2rem;-webkit-animation-name:dot1;animation-name:dot1;-webkit-animation-duration:0.4s;animation-duration:0.4s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-delay:-0.1s;animation-delay:-0.1s}

.users{grid-area:users;border-left:2px solid rgba(0,0,0,0.144);overflow-y:auto}.users__row{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.users__nametag{font-size:0.7rem;font-weight:700;text-transform:uppercase;color:#161515;z-index:2}

.footer{margin-top:1rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.footer__text{text-align:center;font-size:0.8rem;font-weight:600;color:#161515}.footer__icon{font-size:0.3rem;margin:0 0.5rem;color:#161515}.footer__link{color:#161515}.footer__link:hover{color:#08ccab}

*{box-sizing:border-box;margin:0;padding:0}

body{background:#c5c7f6;background:linear-gradient(65deg, #b3b6f5 0%, #daeafb 100%)}::-webkit-scrollbar{width:0.9rem}::-webkit-scrollbar-track{background:#cecece}::-webkit-scrollbar-thumb{background:#3e8ecf}::-webkit-scrollbar-thumb:hover{background:#3e8ecf}#root{height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:1rem;padding-bottom:0}*{font-family:'Nunito', sans-serif}

