@import 'styles/variables';

.chat {
  grid-area: chat;
  display: grid;
  grid-template-rows: max-content 1fr max-content;
  overflow: hidden;
}

.chat__channeltag {
  padding: 0.25rem 1rem;
  color: $dark;
  font-weight: 400;
  font-size: 1.5rem;
  border-bottom: 2px solid rgba(0, 0, 0, 0.144);
  box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.041);
}

.chat__channeltag span {
  margin-right: 0.2rem;
  font-weight: 700;
}


.chat__messages {
  overflow-y: auto;
  padding: 1rem;
}

.chat__content {
  display: flex;
  align-items: center;
}

.chat__content + .chat__content {
  margin-top: 1rem;
}

.chat__left {
  margin-right: 1rem;
}

.chat__right {
  position: relative;
  background-color: $light;
  padding: 0.5rem;
  border-radius: 0.35rem;
  box-shadow: 1px 1px 0 0 rgba(0, 0, 0, 0.137);
  flex: 1;
}

.chat__pointer {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%) translateX(-15%) rotate(45deg) skew(-20deg, -20deg);
  width: 1rem;
  min-width: 1rem;
  height: 1rem;
  min-height: 1rem;
  background-color: $light;
  z-index: 1;
}

.chat__content-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat__nametag {
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
  color: $dark;
  z-index: 2;
}

.chat__timetag {
  font-size: 0.7rem;
  font-weight: 700;
  color: $faded;
  z-index: 2;
}

.chat__texttag {
  position: relative;
  font-size: 0.9rem;
  margin-top: 0.2rem;
  word-break: break-all;
  color: $dark;
  z-index: 100;
}

.chat__form-container {
  padding: 1.5rem 1rem;
  border-top: 2px solid rgba(0, 0, 0, 0.144);
  position: relative;
}

.chat__form {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
}

.chat__input {
  border-radius: 0.5rem;
  flex: 1;
  border: none;
  outline: none;
  background-color: $light;
  border: 1px solid $faded;
  font-weight: 300;
  font-size: 0.9rem;
  color: $dark;
  padding: 0.5rem;
}

.chat__button {
  border-radius: 0.5rem;
  margin-left: 0.5rem;
  padding: 0.5rem;
  background-color: $primaryblock;
  border: none;
  outline: none;
  color: $light;
  text-transform: uppercase;
  font-size: 0.7rem;
  font-weight: 800;
  cursor: pointer;
}

.chat__button:hover {
  background-color: $hoverprimary;
}

@keyframes dot1 {
  0% {transform: translateY(-40%);}
  100% {transform: translateY(0%);}
}

.chat__typingalert {
  font-style: italic;
  font-size: 0.8rem;
  font-weight: 400;
  position: absolute;
  top: 0;
  left: 1rem;
  z-index: 100;
  padding: 0.2rem;
  border-radius: 0.5rem;
}

.chat__dot1 {
  display: inline-block;
  margin: 0rem 0.2rem;
  animation-name: dot1;
  animation-duration: 0.4s;
  animation-iteration-count: infinite;
  animation-delay: -0.3s;
}

.chat__dot2 {
  display: inline-block;
  margin: 0rem 0.2rem;
  animation-name: dot1;
  animation-duration: 0.4s;
  animation-iteration-count: infinite;
  animation-delay: -0.2s;
}

.chat__dot3 {
  display: inline-block;
  margin: 0rem 0.2rem;
  animation-name: dot1;
  animation-duration: 0.4s;
  animation-iteration-count: infinite;
  animation-delay: -0.1s;
}


